import React, { useCallback } from 'react';
import { CreateAccount } from './SignUp';
import { useSelector, useDispatch } from 'react-redux';
import { pushSignUp, saveSignUpField, clearAll } from '../../modules/signUp/signUpActions';
import Notification from '../../utils/notifications';
import { findCurrentInputError, normalizeInputName } from '../../utils/findCurrentInputError';

export const CreateAccountContainer = () => {
  const { errors, input, status } = useSelector((state) => state.signUp.newUser);

  const dispatch = useDispatch();
  const handleChange = useCallback(
    ({ currentTarget }) => {
      let { name, checked } = currentTarget;
      let value = currentTarget.type === 'checkbox' ? currentTarget.checked : currentTarget.value;
      dispatch(saveSignUpField({ name, value, checked }));
    },
    [errors, input, status]
  );

  const handleSubmit = useCallback(
    (e) => {
      e.preventDefault();
      dispatch(pushSignUp());
      if (!Object.keys(errors).length && Object.values(input).filter(Boolean).length === 5) {
        //status==="SUCCESS" token
        dispatch(clearAll());
      }
    },
    [errors]
  );
  if (!Object.keys(errors).length && Object.values(input).filter(Boolean).length === 5) {
    //status==="SUCCESS" token
    return (
      <p>
        Thanks a lot for completing the registration progress. A welcome message with further instructions has been sent to your email address
      </p>
    );
  }

  return (
    <CreateAccount
      input={input}
      errors={errors}
      handleChange={handleChange}
      handleSubmit={handleSubmit}
      Notification={Notification}
      findCurrentInputError={findCurrentInputError}
      normalizeInputName={normalizeInputName}
    />
  );
};
