import React, { useState } from 'react';
import closeButtonStyle from './closeButton.module.scss';

import PropTypes from 'prop-types';

const CloseButton = (props) => {
  const [closeFill, setCloseFill] = useState('#333333');

  const closeRollOver = () => {
    if (closeFill === '#333333') {
      setCloseFill('#999999');
    }
  };
  const closeRollOut = () => {
    setCloseFill('#333333');
  };

  return (
    <div
      onMouseOver={() => {
        closeRollOver();
      }}
      onMouseOut={() => {
        closeRollOut();
      }}
      onClick={props.closeClick}
      className={closeButtonStyle.closeBtn}
    >
      <svg
        className={closeButtonStyle.closeSVG}
        width={props.width}
        height={props.height}
        viewBox="0 0 450 450"
      >
        <g>
          <path
            fill={closeFill}
            d="M434.835,383.175l-158.13-158.13L434.34,67.635c14.22-14.22,14.22-37.305,0-51.525c-14.22-14.22-37.305-14.22-51.524,0
		L225.18,173.52L66.915,15.255c-14.22-14.22-37.305-14.22-51.525,0c-14.22,14.22-14.22,37.305,0,51.525L173.61,225L15.165,383.22
		c-14.22,14.221-14.22,37.306,0,51.525c14.22,14.22,37.305,14.22,51.525,0l158.445-158.22L383.31,434.7
		c14.221,14.22,37.306,14.22,51.525,0S449.055,397.395,434.835,383.175z"
          />
        </g>
      </svg>
    </div>
  );
};

export default CloseButton;

CloseButton.propTypes = {
  closeClick: PropTypes.func,
  width: PropTypes.number,
  height: PropTypes.number
};
