import React, { useEffect, useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import menuStyle from './menuGlobal.module.scss';
import menuData from '../../../assets/data/menuGlobalData.json';
import BurgerButton from '../../UI/burger/burgerButton';
import CloseButton from '../../UI/close/closeButton';

const MenuGlobal = (props) => {
  const [mobileMenuVis, setMobileMenuVis] = useState(false);
  const [navBackground, setNavBackground] = useState(false);
  const navRef = useRef();

  navRef.current  = navBackground;

  useEffect(() => {
    mobileMenuVis
      ? (document.body.style.overflow = 'hidden')
      : (document.body.style.overflow = 'visible');
  });

  useEffect(() => {
    const handleScroll = () => {
      const show = window.scrollY > window.innerHeight;
      if (navRef.current !== show) {
        setNavBackground(show);
      }
    };
    document.addEventListener('scroll', handleScroll);
    return () => {
      document.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const burgerClicked = () => {
    setMobileMenuVis(true);
  };
  const closeClick = () => {
    setMobileMenuVis(false);
  };

  const getNav = () => {
    let theNav = [];
    for (let prop in menuData.nav) {
      if ( menuData.nav.hasOwnProperty(prop) ) {
        theNav.push(
          <Link
            className={menuStyle.navButton}
            to={menuData.nav[prop].path}
            key={menuData.nav[prop].title}
          >
            {menuData.nav[prop].title}
          </Link>
        );
      }
    }
    theNav.push(
      <Link className={menuStyle.navButton} to={menuData.signIn.path} key={menuData.signIn.title}>
        {menuData.signIn.title}
      </Link>
    );
    return theNav;
  };

  const getNavMobile = () => {
    let theNav = [];
    for (let prop in menuData.nav) {
      if ( menuData.nav.hasOwnProperty(prop) ) {
        theNav.push(
          <div
            key={menuData.nav[prop].title}
            className={menuStyle.navMobileButtonHolder}
            onClick={closeClick}
          >
            <Link className={menuStyle.navMobileButton} to={menuData.nav[prop].path}>
              {menuData.nav[prop].title}
            </Link>
          </div>
        );
      }

    }
    theNav.push(
      <Link
        className={menuStyle.navMobileButton}
        to={menuData.signIn.path}
        key={menuData.signIn.title}
        onClick={closeClick}
      >
        {menuData.signIn.title}
      </Link>
    );

    return theNav;
  };

  return (
    <div className={menuStyle.menuHolder}>
      <div
        className={mobileMenuVis ? menuStyle.mobileMenuHolder : menuStyle.mobileMenuHolderHidden}
      >
        <div className={menuStyle.topBarMobileWrapper}>
          <nav onClick={closeClick} className={menuStyle.svgWrapper}>
            <Link to="/">
              <span>{menuData.title}</span>
            </Link>
          </nav>
          <nav className={menuStyle.closeWrapper}>
            <CloseButton closeClick={closeClick} width={20} height={20} />
          </nav>
        </div>
        <div className={menuStyle.menuMobileNavHolderHolder}>
          <div className={menuStyle.menuMobileNavHolder}>
            <nav className={menuStyle.menuNavMobile}>{getNavMobile()}</nav>
          </div>
        </div>
      </div>

      <div
        className={
          !navBackground && props.purpleNav
            ? menuStyle.fullMenuHolder
            : menuStyle.fullMenuHolderWhite
        }
      >
        <div className={menuStyle.wrapper}>
          <nav className={menuStyle.svgWrapper}>
            <Link to="/">
              <span>{menuData.title}</span>
            </Link>
          </nav>
          <nav className={menuStyle.menuNav}>{getNav()}</nav>
          <nav className={menuStyle.burgerHolder}>
            {<BurgerButton burgerClicked={burgerClicked} />}
          </nav>
        </div>
      </div>
    </div>
  );
};

export default MenuGlobal;
