import React from 'react';
import { Switch } from 'react-router-dom';
import { PublicRoute } from '../utils/customRoutes';
import routing from '../config/routing';
import PromoPage from '../pages/Promo/Promo';
import { CreateAccount } from '../pages/SignUp/SignUp';
import SignInContainer from '../pages/SignIn/SignInContainer';
import { CreateAccountContainer } from '../pages/SignUp/SignUpContainer';
import ForgotPasswordContainer from '../pages/ForgotPassword/ForgotPasswordContainer';

function App() {
  return (
    <div className="App">
      <Switch>
        <PublicRoute exact path={routing().root} component={PromoPage} />
        <PublicRoute exact path={routing().createAccount} component={CreateAccountContainer} />
        <PublicRoute path={routing().login} component={SignInContainer} />
        <PublicRoute path={routing().forgotPassword} component={ForgotPasswordContainer} />
      </Switch>
    </div>
  );
}

export default App;
