import React from 'react';
import { Label, Input, FormFeedback, FormGroup } from 'reactstrap';
import './TextInput.css';

const TextInput = ({
  name,
  type = 'text',
  labelText,
  labelIcon,
  note,
  onChange,
  error,
  className,
  forwardRef,
  children,
  ...rest
}) => {
  const key = `${name}_${type}_input`;

  const classes = ['input__block'];

  if (error) {
    classes.push('error');
  }

  if (className) {
    classes.push(className);
  }

  return (
    <FormGroup className={classes.join(' ')}>
      <Label htmlFor={key} className="input__label">
        {labelIcon}
        {labelText}
      </Label>
      {type === 'area' ? (
        <Input
          type="textarea"
          id={key}
          className="input__field"
          onChange={onChange}
          name={name}
          ref={forwardRef}
          {...rest}
        />
      ) : error ? (
        <Input
          invalid
          id={key}
          className="input__field"
          onChange={onChange}
          name={name}
          type={type}
          ref={forwardRef}
          min={0}
          {...rest}
        />
      ) : (
        <Input
          id={key}
          className={className}
          onChange={onChange}
          name={name}
          type={type}
          ref={forwardRef}
          min={0}
          {...rest}
        />
      )}
      {(note || error) && <FormFeedback className="input__note">{error || note}</FormFeedback>}
      {children}
    </FormGroup>
  );
};

export default TextInput;
