import React, { useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import * as actions from '../../modules/resetPassword/resetActions';
import ForgotPassword from './ForgotPassword';
import Notification from '../../utils/notifications';
import { resetSelectors } from '../../modules/resetPassword/resetSelectors';

const ForgotPasswordContainer = () => {
  const dispatch = useDispatch();
  const router = useHistory();
  const errors = useSelector(resetSelectors.getResetErrors);

  // const handleSubmit = async (e) => {
  //   e.preventDefault();
  //   await dispatch(actions.pushResetPassword());

  //   if (!errors.email) {
  //     // dispatch(actions.clearResetEmail());
  //     // dispatch(actions.clearResetErrors());
  //     Notification.info(
  //       'We are sending instructions to reset your password. If you do not receive an email, check your spam folder or make sure this email address is registered with Paidiem.'
  //     );
  //     // router.push('/login');
  //   }
  // };

  const handleSubmit = useCallback(
    async (e) => {
      e.preventDefault();
      await dispatch(actions.pushResetPassword());
      if (!Object.keys(errors).length) {
        dispatch(actions.clearResetEmail());
        dispatch(actions.clearResetErrors());
        Notification.info(
          'We are sending instructions to reset your password. If you do not receive an email, check your spam folder or make sure this email address is registered with Paidiem.'
        );
        router.push('/login');
      }
    },
    [dispatch, errors, router]
  );

  const handleChange = useCallback(
    ({ currentTarget }) => {
      const { value } = currentTarget;
      dispatch(actions.saveResetEmailField(value));
    },
    [dispatch]
  );
  return <ForgotPassword handleChange={handleChange} handleSubmit={handleSubmit} errors={errors} />;
};

export default ForgotPasswordContainer;
