import React from 'react';
import { Link } from 'react-router-dom';
import menuFooterStyle from './menuFooter.module.scss';
import PropTypes from 'prop-types';

const MenuFooter = (props) => {
  const getNav = () => {
    let navList = [];

    for (let item in props.menuData) {
      navList.push(
        <Link
          className={menuFooterStyle.navButton}
          to={props.menuData[item].path}
          key={props.menuData[item].title}
        >
          {props.menuData[item].title}
        </Link>
      );
    }

    return navList;
  };

  return (
    <div className={menuFooterStyle.wrapper}>
      <nav className={menuFooterStyle.menuNav}>{getNav()}</nav>
    </div>
  );
};

export default MenuFooter;

MenuFooter.propTypes = {
  menuData: PropTypes.object
};
