const INVALID = 'INVALID';
const LENGTH = 'LENGTH';
const REQUIRED = 'REQUIRED';
const INFO = 'INFO';
const MATCH = 'MATCH';
const CONFIRM = 'CONFIRM';

function generate({ field, min, max, messages }) {
  return {
    [LENGTH]: `The ${field} must be ${max ? '' : 'min '}${min}${
      max ? '-' + max : ''
    } characters long`,
    [REQUIRED]: `The ${field} is required`,
    [INVALID]: `The ${field} is invalid`,
    [CONFIRM]: `The confirm password is required`,
    ...messages
  };
}

export { INVALID, LENGTH, REQUIRED, CONFIRM, INFO, MATCH, generate };
