import React, { useState } from 'react';
import burgerWrapperStyle from './burgerButton.module.scss';
import PropTypes from 'prop-types';

const BurgerButton = (props) => {
  const [burgerFill, setBurgerFill] = useState('#333333');

  const burgerRollOver = () => {
    if (burgerFill === '#333333') {
      setBurgerFill('#999999');
    }
  };
  const burgerRollOut = () => {
    setBurgerFill('#333333');
  };

  return (
    <div
      className={burgerWrapperStyle.burgerWrapper}
      onMouseOver={() => {
        burgerRollOver();
      }}
      onMouseOut={() => {
        burgerRollOut();
      }}
      onClick={props.burgerClicked}
    >
      <svg x="0px" y="0px" width="30px" height="30px" viewBox="0 0 50 50">
        <path fill={burgerFill} d="M0,7.5v5h50v-5H0z M0,22.5v5h50v-5H0z M0,37.5v5h50v-5H0z" />
      </svg>
    </div>
  );
};

export default BurgerButton;

BurgerButton.propTypes = {
  burgerClicked: PropTypes.func
};
