import validate from './utils/validate';

export default function ({ email, First_Name, Last_Name, Password, agreement }) {
  let errors;

  errors = {
    ...validate.length({ field: 'Password', value: Password }),
    ...validate.length({ field: 'First_Name', value: First_Name }),
    ...validate.length({ field: 'Last_Name', value: Last_Name }),
    ...validate.email(email),
    ...validate.checkbox(agreement)
  };

  return {
    errors,
    isValid: !Object.keys(errors).length
  };
}
